@keyframes loading-indicator {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#mask {
  position: fixed;
  width: 0;
  height: 0;
  overflow: hidden;
  top: 0;
  left: 0;
  opacity: 0;
  background: hsla(0, 0%, 0%, 0.95);
  background-color: hsla(0, 0%, 0%, 0.9);
}
@supports (backdrop-filter: saturate(180%) blur(0.5rem)) {
  #mask {
    background-color: hsla(0, 0%, 0%, 0.8);
    backdrop-filter: saturate(180%) blur(0.5rem);
  }
}
#mask.loading::before {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: calc(50% - 5rem);
  left: calc(50% - 5rem);
  border: 0.8rem solid #efefef;
  border-top-color: #666666;
  border-bottom-color: #666666;
  border-radius: 50%;
  animation: loading-indicator 1.25s ease 8;
}
#mask h1 {
  padding: 0.2rem 0;
  font-size: 1.8rem;
  color: #efefef;
}
@media only screen and (min-width: 768px) {
  #mask h1 {
    text-align: center;
  }
}
#mask img {
  width: 100%;
  height: calc(100% - 170px);
  margin: 2rem auto;
  z-index: 2;
  object-fit: contain;
}
#mask.enabled {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 2rem;
  z-index: 1000;
  opacity: 1;
}
#mask.screens {
  justify-content: space-between;
}

#mask-open-tab {
  width: 3rem;
  height: 3rem;
  padding-left: 3rem;
  position: absolute;
  top: 2rem;
  right: 6rem;
  background: url("/images/icons.svg") no-repeat;
  background-size: auto 2rem;
  background-position: -10.2rem 0.5rem;
  overflow: hidden;
}

#mask-close {
  width: 3rem;
  height: 3rem;
  padding-left: 3rem;
  position: absolute;
  top: 2rem;
  right: 1.5rem;
  background: url(/images/icons.svg) no-repeat;
  background-size: auto 2rem;
  background-position: -12.8rem 0.5rem;
  overflow: hidden;
}

a.new-tab {
  display: flex;
  justify-content: center;
  position: relative;
}
a.new-tab::after {
  content: "";
  display: block;
  width: 4rem;
  height: 4rem;
  position: absolute;
  background: url("/images/icons.svg") no-repeat;
  background-size: cover;
  background-position: -32rem 0;
}
@media (hover: hover) {
  a.new-tab::after {
    top: calc(50% - 2rem);
    left: calc(50% - 2rem);
    opacity: 0;
    transition: opacity 0.2s;
  }
  a.new-tab img {
    transition: filter 0.2s;
  }
  a.new-tab:hover::after {
    opacity: 1;
  }
  a.new-tab:hover img {
    filter: brightness(50%);
  }
}
@media (hover: none) {
  a.new-tab::after {
    display: none;
  }
}

